* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #f2f2f2;
    margin: 0;
    padding: 0;
    font-family: Roboto;
}

.container {
    width: 1170px;
    height: 100vh;
    margin: 40px auto;
    border-radius: 20px;
    position: relative;
}

.loader {
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.movies {
    padding: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 60px;
}
