
.movies__movie {
    display: flex;
    justify-content: start;
    flex-direction: column;
    align-items: center;
    position: relative;
    background: #fff;
}

.movies__movie img {
    width: 20%;
    position: absolute;
    left: -10px;
    top: -10px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.3);
}

.movie__data {
    padding: 10px 10px 20px;
    margin-left: 10px;
    color: #999;
    padding-left: 100px;
}

.movie__data .movie__title {
    text-align: left;
    font-size: 24px;
    color: #444;
    margin: 10px 0;
}
.movie__data .movie__year {
    margin-bottom: 16px;
}
.movie__data .movie__summary {
    font-size: 0.7rem;
    margin-bottom: 10px;
}
.movie__data .movie__genres {
    list-style: none;
    display: flex;
    font-size: 0.8rem;
    text-transform: uppercase;
}
.movie__data .movie__genres li {
    margin-right: 4px;
    padding: 4px;
    background: #999;
    color: #fff;
}